.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


/* App.css */

.container {
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
}

h1 {
  margin-top: 20px;
}

.orientation-selector {
  margin: 20px 0;
}

.orientation-selector label {
  margin-right: 10px;
}

.editor-preview {
  display: flex;
  justify-content: center;
  align-items: center;
}

.editor {
  width: 400px;
  height: 400px;
  position: relative;
  background: #333;
  color: #fff;
}

.placeholder {
  width: 400px;
  height: 400px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #eee;
  color: #888;
}

.crop-area {
  border: 2px solid green; /* 뷰포트 사각형을 녹색으로 표시 */
}

.preview {
  margin-left: 20px;
  width: 400px;
  height: 400px;
  background: #eee;
  display: flex;
  justify-content: center;
  align-items: center;
}

.preview img {
  max-width: 100%;
  max-height: 100%;
}

.controls {
  margin: 20px 0;
}

.controls label {
  margin-right: 10px;
}

button {
  padding: 10px 20px;
  font-size: 16px;
}
